@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&display=swap);
* {
  font-family: "Lato", sans-serif;
}

.navbar {
  background-color: white;
  margin-bottom: 15px;
  border-bottom: 1px solid black;
}
.navbar .navbar-buttons {
  text-align: left;
}
.navbar .navbar-buttons button {
  font-family: "Lato", sans-serif;
  font-weight: 700;
  padding-top: 15px;
  padding-bottom: 15px;
  margin: 5px;
  background-color: white;
  border: none;
  font-size: large;
  transition: 0.5s;
}
.navbar .navbar-buttons button:hover {
  background-color: #1e702f;
  color: white;
}
.navbar .navbar-buttons p {
  display: inline;
  font-size: large;
}

.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  overflow: auto;
  background-color: white;
  padding-top: 60px;
  text-align: center;
}
.modal .modal-body {
  width: 50vw;
  margin: 0 auto;
}
.modal .modal-body input {
  width: 100%;
  height: 25px;
  margin-bottom: 10px;
}
.modal .modal-body .modal-body-buttons button {
  background-color: white;
  border: none;
  font-size: large;
  margin: 15px;
  padding-top: 10px;
  padding-bottom: 10px;
  transition: 0.5s;
}
.modal .modal-body .modal-body-buttons button:hover {
  background-color: #1e702f;
  color: white;
}

.modal-display {
  display: inline;
}

.modal-none {
  display: none;
}

